@charset "utf-8";

body{
  background-color:aliceblue;
}
.movie{
  margin: 0px auto;
  display: grid;
  width: 1320px;
  grid-gap:120px;
  grid-template-columns: repeat(2,600px);
  padding-top:150px ;
  padding-bottom:150px ;
}
article{
  height: 370px;
  background-color: #fff;
  position: relative;
  box-shadow: 4px 4px 12px 4px rgba(0, 0, 0, 0.507);
}
article img{
  position: absolute;
  left: 20px;
  top: -80px;
  box-shadow: 4px 4px 8px 4px rgba(204, 204, 204, 0.507);
}
article div{
  position: absolute;
  width: 280px;
  height: 370px;
  right: 20px;
  top: 0px;
}
article div h3{
  margin: 10px 0px;
  width: 280px;
  font-weight: 100;
  font-size: 30px;
}
article div p{
  color: #ccc;
}
article div p:last-child{
  line-height: 26px;
}
